$font: 'NT Somic', Arial, Helvetica, sans-serif;
$std-size: 1.6rem;
$radius: 0.4rem;

$desktop: 1350px;
$tablet: 1024px;
$mobile: 840px;

$black: #0A0A0A;
$black-light: #202020;
$grey: #B6B6B6;
$grey1: #F5F5F5;
$grey2: #F0F0F0;
$grey3: #DBDBDB;
$grey-dark1: #B6B6B6;
$grey-dark2: #8A8A8A;
$grey-soft: #ECECEC;
$pink: #FF197A;
$white: #FAFAFA;
$yellow: #FFCF4B;
$violet: #8123FF;
$violet-bg: #D5BBF9;
$beige: #F88D8D;
$beige-bg: #FFF3F3;
$coral-bg: #FF9A9A;
$scarlet-bg: #FF573D;
$green-bg: #3DCF92;
$orange: #FFAC4B;

/* Common start */
html {
  font-size: 62.5%; }

body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  width: 100%;
  min-width: 39rem;
  margin: 0 auto;
  font-family: $font;
  font-weight: 400;
  font-size: $std-size;
  line-height: 130%;
  color: $black;
  background-color: $white; }

h1, h2, h3, h4, h5, h6 {
  font-family: $font;
  font-weight: 700;
  color: $black; }

h1, .h1 {
  font-size: 4rem;
  line-height: 110%;

  @media screen and (max-width: $tablet) {
    font-size: 2.8rem;
    line-height: 130%; } }

h2, .h2 {
  font-size: 2.8rem;
  line-height: 130%;

  @media screen and (max-width: $tablet) {
    font-size: 1.6rem; } }

h3, .h3 {
  font-size: 1.6rem;
  line-height: 130%;

  @media screen and (max-width: $tablet) {
    font-size: 1.4rem; } }

.h3-1--pink {
  font-size: 2rem;
  line-height: 130%;
  color: $pink;

  @media screen and (max-width: $tablet) {
    font-size: 1.8rem; } }

h4, .h4 {
  font-size: 1.4rem;
  line-height: 130%;

  @media screen and (max-width: $tablet) {
    font-size: 1.2rem;
    line-height: 110%; } }

h5, .h5 {
  font-size: 1.2rem;

  @media screen and (max-width: $tablet) {
    font-size: 1rem; } }

h6, .h6 {
  font-size: 1.1rem;

  @media screen and (max-width: $tablet) {
    font-size: .8rem; } }

input {
  accent-color: $pink; }

label {
  padding-left: .8rem; }

.checkbox, .radio {
  // width: fit-content
  display: grid;
  grid-template-columns: 2rem 1fr;
  justify-content: center;
  align-items: start; }

input[type="checkbox"] {
  appearance: none;
  position: relative;
  width: 2rem;
  height: 2rem;
  background: #fff;
  // box-shadow: inset 0 0 5px rgb(0 0 0 / 0.2)
  transition: 500ms;
  border: .1rem solid $grey2;
  border-radius: $radius;

  &::after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    left: .2rem;
    top: .2rem;
    background-image: url('../images/icons/checkbox-white.svg');
    background-repeat: no-repeat;
    transition: 500ms; }

  &:hover {
    border: .1rem solid $pink;
    border-radius: $radius;
    cursor: pointer; }

  &:checked {
    background-color: $pink;
    background-size: contain;
    border: .1rem solid $pink;
    border-radius: $radius;

    &::after {
      width: 2rem;
      height: 2rem;
      transition: 500ms; } } }

summary {
  display: flex;
  justify-content: space-between;
  padding: 2.4rem 0;
  font-size: 1.6rem;
  line-height: 130%;
  font-weight: 500;
  cursor: pointer;
  width: 100%;

  &::-webkit-details-marker {
    display: none; }

  &::-moz-list-bullet {
    list-style-type: none; }

  &::marker {
    display: none; }

  &:after {
    content: "+";
    display: inline-block;
    color: $pink;
    font-size: 2rem;
    font-weight: 400; }

  &:focus {
    outline: 0; } }

details {
  display: block;
  color: $grey-dark2;

  summary {
    color: $black; }

  &[open] > summary:after {
    content: "â€”"; } }
dialog {
  max-width: 100%;
  max-height: 100%; }

.desktop--block {
  display: block;

  @media screen and (max-width: $tablet) {
    display: none; } }

.mobile--block {
  display: none;

  @media screen and (max-width: $tablet) {
    display: block; } }

.desktop--grid {
  display: grid;

  @media screen and (max-width: $tablet) {
    display: none; } }

.mobile--grid {
  display: none;

  @media screen and (max-width: $tablet) {
    display: grid; } }

.desktop--flex {
  display: flex;

  @media screen and (max-width: $tablet) {
    display: none; } }

.mobile--flex {
  display: none;

  @media screen and (max-width: $tablet) {
    display: flex; } }

.button {
  padding: 1rem 1.8rem;
  display: inline-block;
  width: fit-content;
  border-radius: $radius;
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.8rem;
  cursor: pointer; }

.button--pink {
  color: $white;
  background-color: $pink;
  transition: 0.2s linear;

  &:hover {
    background-color: $black; } }

.button--black {
  color: $white;
  background-color: $black;
  transition: 0.2s linear;

  &:hover {
    background-color: $pink; } }

.button--white {
  color: $black;
  background-color: #fff;
  transition: 0.2s linear;

  &:hover {
    background-color: $black;
    color: #fff; } }

.header,
.main,
.footer {
  width: 100%; }

.section {
  padding: 0 1.5rem; }

.section_heading {
  display: block;
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.6rem; }

.wrapper--global {
  max-width: 138rem;
  margin: 0 auto;

  @media screen and (max-width: $tablet) {
    max-width: 100%; } }


.icon--search-white {
  background-image: url(../images/icons/search-white.svg);
  background-size: cover; }

.icon--heart-white {
  background-image: url(../images/icons/heart-white.svg);
  background-size: cover; }

.icon--cart-white {
  background-image: url(../images/icons/cart-white.svg);
  background-size: cover; }

.icon--nav-white {
  background-image: url(../images/icons/nav-white.svg);
  background-size: cover; }

.icon--add-to-cart-pink {
  background-image: url("../images/icons/cart-pink.svg"); }

.icon--address {
  background-image: url('../images/contacts-geo.svg'); }

.icon--metro {
  background-image: url('../images/contacts-metro.svg'); }

.icon--phone {
  background-image: url('../images/contacts-phone.svg'); }

.icon--email {
  background-image: url('../images/contacts-email.svg'); }

.icon--days {
  background-image: url('../images/contacts-calendar.svg'); }

.icon--hours {
  background-image: url('../images/contacts-clock.svg'); }

.link--grey {
  color: $grey1;
  transition: 0.2s linear;

  &:hover {
    color: $pink; } }

.left {
  text-align: left; }

.right {
  text-align: right; }

.center {
  text-align: center; }

.column__left {
  justify-content: left; }

.column__center {
  justify-content: center; }

.column__right {
  justify-content: right; }

.link_icon--account {
  background-image: url("../images/icons/tzm-icon.svg");
  transition: 0.2s linear;

  &:hover {
    background-image: url("../images/icons/tzm-icon-pink.svg"); } }

.link_icon--search {
  background-image: url("../images/icons/search.svg");
  transition: 0.2s linear;

  &:hover {
    background-image: url("../images/icons/search-pink.svg"); } }

.link_icon--favourites {
  background-image: url("../images/icons/heart.svg");
  transition: 0.2s linear;

  &:hover {
    background-image: url("../images/icons/heart-pink.svg"); } }

.link_icon--cart {
  background-image: url("../images/icons/cart.svg");
  transition: 0.2s linear;

  &:hover {
    background-image: url("../images/icons/cart-pink.svg"); } }

.flex_columns {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  width: 100%; }

.flex_columns__item {
  display: flex;
  justify-content: space-between;
  font-size: 1.4rem;
  line-height: 130%; }

.flex_columns__item__dots {
  min-width: 1rem;
  flex: 1 0;
  border-bottom: 1px dotted #000;
  height: 1rem;
  margin: 0.4em; }

.flex_columns__item__value {
  @media screen and (max-width: $tablet) {
    width: 50%; } }

.circle_bg--black {
  display: inline-block;
  width: 2.2rem;
  height: 2.2rem;
  background-color: $black-light;
  color: $white;
  border-radius: 9rem;
  font-size: 1.4rem;
  font-weight: 500;
  text-align: center; }
/* Common end */

/* Carousel start */
.arrows {
  position: absolute;
  top: 0;
  padding: 0 1+1.5+1.5rem 0 1rem;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  justify-content: space-between;

  @media screen and (max-width: $tablet) {
    display: none; } }

.arrows--left {
  display: none;
  width: 4rem;
  height: 4rem;
  background-image: url('../images/icons/arrow-slider-left-black.svg');
  cursor: pointer;
  opacity: 0.5;
  z-index: 10;
  transition: 0.2s linear;

  &:hover {
    opacity: 1; } }

.arrows--right {
  display: none;
  width: 4rem;
  height: 4rem;
  background-image: url('../images/icons/arrow-slider-right-black.svg');
  cursor: pointer;
  opacity: 0.5;
  z-index: 10;
  transition: 0.2s linear;

  &:hover {
    opacity: 1; } }

.dots {
  bottom: 2rem !important;
  top: auto !important;
  left: 0 !important;
  width: 100% !important;
  position: absolute;
  text-align: center;
  transition: .3s opacity;
  transform: translate3d(0, 0, 0);
  z-index: 10; }

.dots__item {
  display: inline-block;
  margin: 0 0.2rem;
  width: 0.6rem;
  height: 0.6rem;
  border-radius: $radius;
  background-color: $grey3;
  cursor: pointer; }

.dots__item.tns-nav-active,
.dots__item.is-active {
  background-color: $pink; }
/* Carousel end */

/* Breadcrumbs start */
.breadcrumbs {
  padding: 2rem 1.5rem;
  overflow-x: scroll;
  scrollbar-gutter: stable;
  scrollbar-width: none;
  scrollbar-color: $white;

  @media screen and (max-width: $tablet) {
    padding: 1rem 1.5rem; } }

.breadcrumbs__menu {
  display: flex;
  gap: 0.5rem; }

.breadcrumbs__menu__item {
  color: $grey-dark1;
  list-style-type: disc;
  margin-left: 2rem;
  font-size: 1.2rem;
  line-height: 130%;
  white-space: nowrap; }

.breadcrumbs__menu__item--active {
  color: $violet;

  @media screen and (max-width: $tablet) {
    padding-right: 2rem; } }

.breadcrumbs__menu__item__link {}

/* Breadcrumbs end */

/* Header start */
.header__top__container {
  display: flex;
  gap: 3rem;
  justify-content: space-between; }

.header__top__container__menu,
.header__top__container__getintouch {
  display: flex;
  gap: 4rem;
  padding: 0.8rem 0; }

.header__top__container__menu__item,
.header__top__container__getintouch__item {
  font-size: 1.4rem;
  line-height: 130%;
  text-wrap: nowrap; }

.header__top__menu__item__link,
.header__top__getintouch__item__link {
  font-size: 1.4rem;
  line-height: 1.8rem; }

.getintouch__city {
  text-decoration: none;
  border-bottom: 1px dashed;
  transition: 0.2s linear;

  &:link, &:hover {
    text-decoration: none; } }

.getintouch__account {
  padding-left: 2.4rem;
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 1.6rem 1.6rem; }

.header__top {
  background-color: $black-light;
  color: $grey1; }

.header__bottom {
  background-color: $black; }

.header__bottom__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding-top: 0.8rem;
  padding-bottom: 0.8rem; }

.header__bottom__container__column {
  display: flex;
  align-items: center; }

.header__bottom__container__column__button {
  padding: 1rem 1.8rem 1rem 1.8 + 1.6 + 0.5rem;
  background-image: url("../images/icons/nav-white.svg");
  background-position: 1rem center;
  background-repeat: no-repeat;
  background-size: 1.6rem 1.6rem; }

.header__bottom__container__column__logo__image {
  width: 9.2rem;
  height: 4.6rem; }

.header__bottom__container__column__services {
  display: flex;
  gap: 4rem; }

.header__bottom__container__column__services__item__link {
  padding-left: 2.8rem;
  font-size: 1.6rem;
  line-height: 2rem;
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 2rem 2rem; }

.header__mobile {
  @media screen and (max-width: $tablet) {
    padding: 1.2rem 1.5rem;
    grid-template-columns: 4.4rem 4.4rem auto 4.4rem 4.4rem;
    justify-items: center;
    align-items: center;
    gap: 0.4rem;
    background-color: $black;

    .header__mobile__item {
      padding: 1.2rem; }

    .header__mobile__item__icon {
      display: block;
      width: 2rem;
      height: 2rem;
      cursor: pointer; }

    .header__mobile__item--logo {
      justify-self: center;
      align-self: center; }

    .header__mobile__item__logo {
      display: block; }

    .header__mobile__item__logo__image {
      width: 10rem;
      height: 5rem; } } }
/* Header end */

/* Main -- Banner start */
.banner {
  position: relative;
  height: 45rem;
  width: 100%; }

.banner__container {
  position: relative;
  // display: flex
  width: 100%;
  height: 100%;
  z-index: 1;
  box-sizing: content-box; }

.banner__container__item {
  position: relative;
  display: block;
  height: 45rem;
  width: 100%; }

.banner__container__item__image {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  z-index: -1; }

.banner__arrows {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 138rem;
  height: 45rem; }

.banner__arrows__button {
  position: absolute;
  top: var(--swiper-navigation-top-offset, 50%);
  width: 2rem;
  height: 2rem;
  z-index: 10;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-repeat: no-repeat; }

.arrow--left {
  background-image: url("../images/icons/arrow-left.svg");
  left: var(--swiper-navigation-sides-offset, 1rem);
  right: auto; }

.arrow--right {
  background-image: url("../images/icons/arrow-right.svg");
  right: var(--swiper-navigation-sides-offset, 1rem);
  left: auto; }

.banner__container__item__content {
  padding: 0 1.5rem;
  width: 100%;
  text-align: center; }

.banner__container__item__content__heading {
  margin-top: 6rem;
  font-weight: 700;
  font-size: 6rem;
  line-height: 7.8rem;
  margin-bottom: 1rem;

  @media screen and (max-width: $tablet) {
    font-size: 4rem;
    line-height: 4.4rem; } }

.banner__container__item__content__text {
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.6rem;
  margin-bottom: 3rem;

  @media screen and (max-width: $tablet) {
    font-size: 1.6rem;
    line-height: 2.1rem;
    margin-bottom: 2rem; } }

.banner__container__item__content__button {
  display: inline-block; }
/* Main -- Banner end */

/* Main -- Categories start */
.categories {
  padding: 6rem 0;

  @media screen and (max-width: $tablet) {
    padding: 2rem 0; } }

.categories__tiles {
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 1rem;

  @media screen and (max-width: $tablet) {
    grid-template-columns: repeat(2, 1fr); } }

.categories__tiles__item {
  padding: 1.6rem;
  background-color: $grey1;
  border-radius: $radius;

  &:hover {
    background-image: url('../images/main-tiles/tile-bg-hover.svg');
    background-size: cover;
    color: $white; }

  @media screen and (max-width: $tablet) {
    padding: 1.6rem 0.8rem; } }


.categories__tiles__item__link {
  display: grid;
  gap: 1rem;
  width: 100%;
  height: 100%;

  @media screen and (max-width: $tablet) {
    display: block; }

  @media screen and (max-width: $tablet) {
    gap: 1rem; } }

.categories__tiles__item__link__image {
  display: block;

  @media screen and (max-width: $tablet) {
    margin: auto; } }

.categories__tiles__item__link__text {
  margin: auto;
  display: block;
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 140%;
  text-align: center;

  @media screen and (max-width: $tablet) {
    font-size: 1.4rem;
    line-height: 130%;
    padding-bottom: 1.6rem; }

  @media screen and (max-width: $tablet) {
    font-size: 1.4rem;
    line-height: 130%;
    padding-bottom: 1.6rem; } }
/* Main -- Categories end */

/* Main -- Hero start */
.hero {
  padding-bottom: 6rem;

  @media screen and (max-width: $tablet) {
    padding-bottom: 0; } }

.hero__container__image {
  width: 100%; }
/* Main -- Hero end */

/* Main -- Brands start */
.brands {
  position: relative;
  margin: 6rem auto;
  width: 100%;
  max-width: 138rem;
  overflow-x: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
      width: 0;
      height: 0; }

  &:hover .arrows--left,
  &:hover .arrows--right {
    display: block; }

  @media screen and (max-width: $tablet) {
    margin: 4rem auto; } }

.brands__list {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
  -ms-overflow-style: none;
  scrollbar-width: none;
  z-index: 1;

  @media screen and (max-width: $tablet) {
    column-gap: 1rem; } }

.brands__list__item {
  margin: 0;
  height: 9rem;
  width: auto; }

.brands__list__item__image {
  height: 9rem;
  width: auto;
  max-width: unset; }
/* Main -- Brands end */

/* Main -- Suggestions start */
.suggestions {
  margin-bottom: 6rem; }

.suggestions__container {
  position: relative;
  margin: 6rem auto;
  width: 100%;
  max-width: 138rem;
  // overflow-x: auto
  overflow-x: hidden;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
      width: 0;
      height: 0; }

  &:hover .arrows--left,
  &:hover .arrows--right {
    display: block; }

  @media screen and (max-width: $tablet) {
    margin: 4rem auto; } }

.suggestions__container .tns-item {
  display: flex !important; }

.suggestions__container__list {
  display: flex;
  position: relative;
  flex-wrap: nowrap;
  overflow: hidden;
  z-index: 1;
  justify-content: flex-start;
  align-items: center;

  &::-webkit-scrollbar {
      width: 0;
      height: 0; }

  @media screen and (max-width: $tablet) {
    align-items: stretch; } }

.suggestions__container__list__item {
  position: relative;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
  min-width: 25.2rem;
  background-color: #fff;
  background-clip: content-box;
  border-radius: $radius;

  @media screen and (max-width: $tablet) {
    min-width: 20rem;
    gap: 1rem; } }

.suggestions__container__list__item__images {
  margin: 2.2rem 2.2rem 0;
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  width: 23rem;
  height: 23rem;
  overflow-x: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
      width: 0;
      height: 0; }

  @media screen and (max-width: $tablet) {
    margin: 1rem 1rem 0;
    width: 18rem;
    height: 18rem; } }

.suggestions__container__list__item__images__link {
  display: block;
  width: 23rem;
  height: 23rem;

  @media screen and (max-width: $tablet) {
    width: 18rem;
    height: 18rem; } }

.suggestions__container__list__item__images__link__image {
  max-width: unset;
  width: 23rem;
  height: 23rem;

  @media screen and (max-width: $tablet) {
    width: 18rem;
    height: 18rem; } }

.suggestions__container__list__item__text {
  margin: 0 2.2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 2rem;
  flex: 1 0 10rem;
  gap: 1rem; }

.suggestions__container__list__item__text__title {
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.8rem;
  width: 100%;
  transition: 0.2s linear;

  &:hover {
    color: $pink; } }

.suggestions__container__list__item__text__info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; }

.suggestions__container__list__item__text__info__prices {
  display: flex;
  gap: 0.4rem;
  align-items: center;

  @media screen and (max-width: $tablet) {
    align-items: flex-start;
    flex-direction: column; } }

.suggestions__container__list__item__text__info__prices__price {
  font-size: 1.6rem;
  line-height: 130%;
  font-weight: 700;
  color: $black; }

.suggestions__container__list__item__text__info__prices__oldprice {
  font-size: 1.2rem;
  line-height: 130%;
  font-weight: 400;
